html { font-size: 100%; }
body { background-color: black; background-image: radial-gradient(rgba(255,255,255,.2), rgba(0,0,0,1)); color: white; height: 100vh; }

h1 { color: rgba(255,255,255,.5); font-size: 1.5rem; font-weight: lighter;}
div.words-contain { margin: 0 auto; width: 90%;}
.word-list-contain { width: 100%; clear: both; }
.word-list-wrap { width: 40%; padding: 0 2.5%; margin: 0 2.5%; display: inline-block; min-height: 250px; float: left; text-align: center;}
.word-list { background: white; color: black; border: 1px solid black; box-shadow: 0 0 5px 3px rgba(255,255,255,.3); padding: 10px 5px; }
.active-words, .removed-words {display:block; width: 100%; margin: 0 auto; font-size: .8rem;}
h3 { font-size: .75rem;}
.description p {font-size: .9rem; text-align: center;}

.words-btn-row { margin: 30px auto 10px auto; text-align: center;}
.word-btn { background: black; border: 1px solid white; border-radius: 3px;  box-shadow: 2px 2px 0px 0px white; color: white; margin: 0 5px; padding: 5px 10px; cursor: pointer; font-size: 1rem; font-weight: bold;}
.word-btn:focus { outline: 0;}
.word-btn.active { background: white; border: 1px solid black; box-shadow: 0px 0px 0px 1px white; color: black; transition: .3s}
.active-letter { margin-top:10px; font-size: 1.5rem; font-weight: 300;}

/* iPhone 6+, 7+, 8+ ----------- */
@media only screen and (min-width: 414px) {
  .active-words, .removed-words { font-size: .9rem;}
  h3 { font-size: .85rem;}
}

/* iPads (landscape) ----------- */
@media only screen and (min-width : 768px) {
  div.words-contain { width: 90%;}
  .active-words, .removed-words { font-size: 1.1rem;}
  h3 { font-size: 1rem;}
  .description p {font-size: 1.2rem; width: 80%; margin: auto;}
  .word-btn { font-size: 1.2rem;}
}


/* Desktops and laptops ----------- */
@media only screen  and (min-width : 1024px) {
  h1 {font-size: 1.5rem; }
  div.words-contain { width: 60%;}
  .active-words, .removed-words { font-size: 1.1rem; padding: 3px 0px}
  h3 { font-size: 1rem;}
  .description p {font-size: 1.2rem; width: 90%;}
  .word-btn { font-size: 1.2rem;}
  .active-letter { font-size: 1.5rem; }
}

/* Large screens ----------- */
@media only screen  and (min-width : 1824px) {
  h1 {font-size: 2rem; }
  div.words-contain { width: 70%;}
  .active-words, .removed-words { font-size: 1.5rem; padding: 3px 0px}
  h3 { font-size: 1.5rem;}
  .description p {font-size: 1.5rem; width: 90%;}
  .word-btn { font-size: 1.5rem; margin: 0 20px; padding: 10px 30px}
  .active-letter { font-size: 2rem; }
}
